.VideoPlayer-App {
  font-family: Arial, sans-serif;
  text-align: center;
  padding: 20px;
}

.VideoPlayer {
  position: relative;
  width: 100%;
  max-width: 800px;
  margin: 0 auto;
}

.ReactPlayer {
  width: 100% !important;
  height: 450px !important;
}

.Camera-List {
  margin-top: 20px;
}

.Camera-Button {
  margin: 5px;
  padding: 10px 20px;
  border: none;
  background-color: #007bff;
  color: white;
  cursor: pointer;
  border-radius: 5px;
  transition: background-color 0.3s;
}

.Camera-Button.Active {
  background-color: #0056b3;
}

.Camera-Button:hover {
  background-color: #0056b3;
}
